import Cookies from 'js-cookie'
import axios from 'axios'
import {
    LOAD_USER_PROFILE_SUCCESS,
    LOAD_USER_PROFILE_FAIL,
    UPDATE_USER_PROFILE_FAIL,
    UPDATE_USER_PROFILE_SUCCESS
} from './types'

export const load_user = () => async dispatch =>{
    const config = {
        headers : {
            'Accept': 'application/json',
            'Content-type': 'application/json'
        }
    };

    try {
        const res = await axios.get('/profile/user/',config)

        if(res.data.error){
            dispatch({
                type: LOAD_USER_PROFILE_FAIL
            })
        } else if(res.data.profile){
            dispatch({
                type: LOAD_USER_PROFILE_SUCCESS,
                payload: res.data
            })
        } else {
            dispatch({
                type: LOAD_USER_PROFILE_FAIL
            })
        }
    } catch (error) {
        dispatch({
            type: LOAD_USER_PROFILE_FAIL
        })
    }
}

export const update_profile = (first_name, last_name, phone, city) => async dispatch => {
    const config = {
        headers:{
            'Accept':'application/json',
            'Content-type':'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({ 
        'withCredentials': true,
        first_name,
        last_name,
        phone,
        city
     });

    try {
        const res = await axios.put('/profile/update/', body, config);

        if(res.data.error){
            dispatch({
                type:UPDATE_USER_PROFILE_FAIL
            });
        } else if(res.data.profile && res.data.username){
            dispatch({
                type:UPDATE_USER_PROFILE_SUCCESS,
                payload: res.data
            });
        } else {
            dispatch({
                type:UPDATE_USER_PROFILE_FAIL
            });
        }
    } catch (error) {
        dispatch({
            type:UPDATE_USER_PROFILE_FAIL
        });
    }

}